import { DefaultObject, isBang } from "../../sdk";
import type { IInletsMeta, IOutletsMeta, IArgsMeta } from "@jspatcher/jspatcher/src/core/objects/base/AbstractObject";

export default class Swap extends DefaultObject<{}, {}, [any, any], [any, any], [any], {}> {
    static description = "Swap input values";
    static inlets: IInletsMeta = [
        {
            isHot: true,
            type: "anything",
            description: "The value to output on the second outlet"
        },
        {
            isHot: false,
            type: "anything",
            description: "The value to output on the first outlet"
        },
    ];
    static outlets: IOutletsMeta = [
        {
            type: "anything",
            description: "The value from the second inlet"
        },
        {
            type: "anything",
            description: "The value from the first inlet"
        }
    ];
    static args: IArgsMeta = [{
        type: "anything",
        optional: true,
        default: 0,
        description: "Initial value to send out the first inlet"
    }];
    _ = { out2: undefined as any, arg: this.args[0] };
    subscribe() {
        super.subscribe();
        this.on("preInit", () => {
            this.inlets = 2;
            this.outlets = 1;
        });
        this.on("updateArgs", (args) => {
            this._.arg = undefined;
            this._.out2 = undefined;
            if (!args || args.length === 0) return;
            this._.arg = args[0];
        });
        this.on("inlet", ({ data, inlet }) => {
            if (inlet === 0) {
                if (!isBang(data)) {
                    try {
                        this._.out2 = data;
                    } catch (e) {
                        this.error(e);
                        return;
                    }
                }
                this.outlet(1, this._.out2);
                this.outlet(0, this._.arg);
            } else if (inlet === 1) {
                this._.arg = data;
            }
        });
    }
}
