import { DefaultObject, isBang } from "../../sdk";
import type { IInletsMeta, IOutletsMeta } from "@jspatcher/jspatcher/src/core/objects/base/AbstractObject";

export default class Mtof extends DefaultObject<{}, {}, [number], [number], [], {}> {
    static package = "electrosmith";
    static author = "beserge";
    static version = "1.0";
    static description = "Convert midi note number to frequency in Hz";
    static inlets: IInletsMeta = [
        {
            isHot: true,
            type: "number",
            description: "Midi Note Number"
        }
    ];
    static outlets: IOutletsMeta = [{
        type: "number",
        description: "Frequency"
    }];
    _ = { freq: undefined as number };
    subscribe() {
        super.subscribe();
        this.on("preInit", () => {
            this.inlets = 1;
            this.outlets = 1;
        });
        this.on("inlet", ({ data, inlet }) => {
            if (inlet === 0) {
                if (!isBang(data)) {
                    try {
                        this._.freq = 440. * Math.exp(0.0577625565 *  (data - 69.));
                    } catch (e) {
                        this.error(e);
                        return;
                    }
                }
                this.outlet(0, this._.freq);
            }
        });
    }
}