import { DefaultObject, isBang } from "../../sdk";
import type { IInletsMeta, IOutletsMeta, IArgsMeta } from "@jspatcher/jspatcher/src/core/objects/base/AbstractObject";

export default class Change extends DefaultObject<{}, {}, [any], [any], [], {}> {
    static description = "Binary Operation";
    static inlets: IInletsMeta = [{
        isHot: true,
        type: "anything",
        description: "The input data stream"
    }];
    static outlets: IOutletsMeta = [{
        type: "anything",
        description: "A value that has changed"
    }];
    _ = { result: undefined as any };
    subscribe() {
        super.subscribe();
        this.on("preInit", () => {
            this.inlets = 1;
            this.outlets = 1;
        });
        this.on("inlet", ({ data, inlet }) => {
            if (inlet === 0) {
                if (!isBang(data)) {
                    try {
                        if (this._.result != data) {
                            this._.result = data;
                            this.outlet(0, this._.result);
                        }
                    } catch (e) {
                        this.error(e);
                        return;
                    }
                } else {
                    this.outlet(0, this._.result);
                }
            }
        });
    }
}
